/* The Modal (background) */

.modal {
  display: block;
  position: fixed;
  z-index: 10;
  padding: 20px 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal.hidden {
  display: none;
}


/* Modal Content */

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 0 20px;
  border: 1px solid #888;
  width: 80%;
  height: 100%;
  overflow: scroll;
}

/* The Close Button */

.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.info-wrapper {
  z-index: auto;
}

/* Banner */
.info-wrapper img {
  margin: 0 4px;
}


